<template>
   <div>
    <v-container grid-list-md >
    <v-flex xs12>
      <v-card >

        <v-list three-line >
          <template v-for="(item, index) in items" >

            <a  @click="openDialog(item)">
            <v-subheader
              v-if="item.header"
              :key="item.header"
            >
              {{ item.header }}
            </v-subheader>

            <v-divider
              v-else-if="item.divider"
              :key="index"
              :inset="item.inset"
            ></v-divider>

            <v-list-tile
              v-else
              :key="item.title"
              avatar
              @click=""
            >
              <v-list-tile-avatar>
                <img :src="item.avatar">
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
                <!-- <v-list-tile-sub-title v-html="item.reverted"> </v-list-tile-sub-title> -->
              </v-list-tile-content>
            </v-list-tile>
          </a>
          </template>
        </v-list>
      </v-card>
    </v-flex>

    <!-- <v-btn
          color="red lighten-2"
          dark
          @click="openDialog()"
        >
          Click Me
        </v-btn> -->


    <div class="text-xs-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
         
          <div v-html="user.title"></div>
        </v-card-title>

        <v-card-text>
          <div v-html="user.subtitle"></div>
        </v-card-text>
        <v-divider></v-divider>
        
        <v-card-text>
          <div>
            <v-text-field
              v-model="user.reverted"
              label="Comment"
            ></v-text-field>
            

          </div>
        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            flat
            @click="dialog = false"
          >
            Revert to User
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>


  </v-container>
   </div>
   
   
    
    
    

</template>
<script>

export default {
    name: 'query',
    created(){
    },
    data () {
      return {
        user:{},
        bio:"Revert to user",
        dialog:false,
        revertcomment:"",
        items: [
          { header: 'Today' },
          {
            userId:1,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
            title: 'Abhimanyu Barge <span style="font-family: wingdings; font-size: 200%;">&#252;</span>',
            subtitle: "<span class='text--primary'>    </span> &mdash; :          .          .           .           .            .     .        . ,           .             .     ,     .    ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,     .",
            reverted : "    :   ",
            visit:true
          },
          { divider: true, inset: true },
          {
            userId:2,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/2.jpg',
            title: 'Sumit Jagtap <span class="grey--text text--lighten-1">4</span>',
            subtitle: "<span class='text--primary'>    </span> &mdash;      .      .           .                          .",
            reverted : "",
            visit:false
          },
          { divider: true, inset: true },
          {
            userId:3,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/3.jpg',
            title: 'Pooja ',
            subtitle: "<span class='text--primary'> ! </span> &mdash; -           .       .              .",
            reverted : "",
            visit:false
          },
          { divider: true, inset: true },
          {
            userId:4,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/4.jpg',
            title: 'swati shinde',
            subtitle: "<span class='text--primary'>  </span> &mdash; -           .       .      .       ,              .                     .?",
            reverted : "",
            visit:false
          },
          { divider: true, inset: true },
          {
            userId:5,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/5.jpg',
            title: 'Avinash kale',
            subtitle: "<span class='text--primary'>    </span> &mdash;               .        -  .   ,            ,          .",
            reverted : "",
            visit:false
          }
        ]
      }
    },
    methods:{
      openDialog(data){
       
        this.user = data
        if(this.user.visit == false){
          this.user.title = this.user.title+' <span style="font-family: wingdings; font-size: 200%;">&#252;</span>'
          this.user.visit = true
        }
        this.dialog = true
      }
    }
}
</script>
<style>
</style>



